import React from 'react';

interface ISettingHead {
  name: string;
  back?: () => void;
}

function LayerSettingHead({name, back}: ISettingHead) {
  return (
    <div className="head">
      {back && (
        <button className="btn_back" onClick={back}>
          <span className="hiddenZone">뒤로</span>
        </button>
      )}
      <h2>{name}</h2>
    </div>
  );
}

export default LayerSettingHead;
