import {Dispatch, SetStateAction, useCallback, useState} from 'react';
import {Swiper} from 'swiper/types';
import {FilterLayer, Header} from './styles/common';
import CheckBox from './element/CheckBox';
import {Minus, Plus} from 'emotion-icons/fa-solid';
import {
  QueryObserverResult,
  RefetchOptions,
  RefetchQueryFilters,
} from '@tanstack/react-query';
import {AxiosError} from 'axios';
import RadioBox from './element/RadioBox';
import {IReceiptListItem, IReceiptListRes} from 'ApiFarm/interface/receipt';
import useSelectModal from 'HookFarm/useSelectModal';
import {kdsSettingStore} from 'MobxFarm/store';
import {runInAction} from 'mobx';
import {observer} from 'mobx-react';

const filterCondition = [
  {id: 0, checkName: '내점'},
  {id: 1, checkName: '포장'},
  {id: 2, checkName: '배달'},
];

interface IKdsHeader {
  receiptsData: IReceiptListItem[];
  swiperRef: Swiper | null;
  slideIdx: number;
  grid: number;
  setGrid: Dispatch<SetStateAction<number>>;
  filter: number[];
  setFilter: Dispatch<SetStateAction<number[]>>;
  setSorting: Dispatch<SetStateAction<string>>;
  refetch: <TPageData>(
    options?: (RefetchOptions & RefetchQueryFilters<TPageData>) | undefined,
  ) => Promise<QueryObserverResult<IReceiptListRes, AxiosError<unknown, any>>>;
}

function KdsHeader({
  receiptsData,
  swiperRef,
  slideIdx,
  grid,
  setGrid,
  filter,
  setFilter,
  setSorting,
  refetch,
}: IKdsHeader) {
  const {showLayer, handleSelectLayer} = useSelectModal();

  const {hideModal} = useSelectModal();

  const handlerAlarm = useCallback(() => {
    runInAction(() =>
      kdsSettingStore.alarm
        ? (kdsSettingStore.alarm = false)
        : (kdsSettingStore.alarm = true),
    );
  }, []);
  const handlerDark = useCallback(() => {
    runInAction(() =>
      kdsSettingStore.dark
        ? (kdsSettingStore.dark = false)
        : (kdsSettingStore.dark = true),
    );
  }, []);

  return (
    <Header>
      <div className="left">
        <h1>
          <span className="hiddenZone">GOPIZZA</span>
        </h1>
        <span className="total_order">{receiptsData.length} ORDERS</span>
        <span className="area_page_show">
          PAGE {slideIdx + 1} /{' '}
          {receiptsData.length === 0
            ? '1'
            : Math.ceil(receiptsData.length / grid)}
        </span>
        <span className="box_set_grid">
          <button
            onClick={() => {
              grid > 4 && setGrid((prev: number) => prev - 4);
              swiperRef?.slideTo(0, 0, false);
            }}
            disabled={4 === grid}>
            <Minus size="16" />
          </button>
          {grid}Grid{' '}
          <button
            onClick={() => {
              12 > grid && setGrid((prev: number) => prev + 4);
              swiperRef?.slideTo(0, 0, false);
            }}
            disabled={12 === grid}>
            <Plus size="16" />
          </button>{' '}
        </span>
      </div>
      <div className="right">
        <div
          className={`area_btn filter ${
            showLayer === 'filterLayer' ? 'on' : ''
          }`}>
          <button
            onClick={() => {
              hideModal();
              handleSelectLayer('filterLayer');
            }}>
            Filter by
          </button>
          {showLayer === 'filterLayer' && (
            <FilterLayer>
              <CheckBox
                checkBoxData={filterCondition}
                checkedList={filter}
                setCheckedLists={setFilter}
              />
            </FilterLayer>
          )}
        </div>
        <div className={`area_btn ${showLayer === 'PrepStatus' ? 'on' : ''}`}>
          <button onClick={() => handleSelectLayer('PrepStatus')}>Prep</button>
        </div>
        <div className={`area_btn ${showLayer === 'SummeryLayer' ? 'on' : ''}`}>
          <button onClick={() => handleSelectLayer('SummeryLayer')}>
            Summary
          </button>
        </div>

        <div className="box_btn">
          <button
            className={`btn_speak ${kdsSettingStore.alarm ? 'on' : ''}`}
            onClick={handlerAlarm}>
            <span className="hiddenZone">Alarm</span>
          </button>
          <button
            className={`btn_recall ${showLayer === 'RecallLayer' ? 'on' : ''}`}
            onClick={() => handleSelectLayer('RecallLayer')}>
            <span className="hiddenZone">History</span>
          </button>
          <button
            className={`btn_setting ${
              showLayer === 'SettingLayer' ? 'on' : ''
            }`}
            onClick={() => handleSelectLayer('SettingLayer')}>
            <span className="hiddenZone">Setting</span>
          </button>
          {/* <button
            className={`btn_dark ${kdsSettingStore.dark ? 'on' : ''}`}
            onClick={handlerDark}>
            <span className="hiddenZone">Theme</span>
          </button> */}
        </div>
      </div>
    </Header>
  );
}

export default observer(KdsHeader);
