import {KdsContent, KdsWrap} from 'ComponentsFarm/styles/common';
import {useEffect, useState} from 'react';
import {Swiper as SwiperContainer, SwiperSlide} from 'swiper/react';
import {Swiper} from 'swiper/types';
import 'swiper/css';
import KdsHeader from 'ComponentsFarm/KdsHeader';
import SwiperSlideItem from 'ComponentsFarm/SwiperSlideItem';

import {useQuery} from '@tanstack/react-query';
import {fetchReceiptList} from 'ApiFarm/receipt';
import {IReceiptListRes} from 'ApiFarm/interface/receipt';
import {AxiosError} from 'axios';
import {Mousewheel} from 'swiper';
import GlobalModal from 'ComponentsFarm/element/GlobalModal';
import {authStore, kdsSettingStore} from 'MobxFarm/store';
import {observer} from 'mobx-react';
import {useHistory} from 'react-router-dom';

function Home() {
  const history = useHistory();
  const [grid, setGrid] = useState(4);
  const [swiperRef, setSwiperRef] = useState<Swiper | null>(null);
  const [slideIdx, setSlideIdx] = useState(0);
  const [filter, setFilter] = useState<number[]>([]);
  const [sorting, setSorting] = useState('processing');

  //셋팅 값 가져오기
  useEffect(() => {
    if (!authStore.session) {
      return history.push('/');
    }
  }, [history]);

  // 영수증 리스트
  const {data, refetch, isFetching} = useQuery<IReceiptListRes, AxiosError>(
    ['receiptListData'],
    () =>
      fetchReceiptList({
        size: 99,
        filter_sale_type: filter.toString(),
        sort_method: sorting, //processing, waiting
      }),
    {
      //refetchInterval: 1000 * 60 * 15,
      refetchInterval: 1000 * 5,
      refetchIntervalInBackground: true,
    },
  );

  console.log('data', data);

  useEffect(() => {
    refetch();
  }, [filter, sorting]);

  return (
    <KdsWrap className={kdsSettingStore.dark ? 'dark' : ''}>
      {data && (
        <KdsHeader
          receiptsData={data.list}
          swiperRef={swiperRef}
          slideIdx={slideIdx}
          grid={grid}
          setGrid={setGrid}
          filter={filter}
          setFilter={setFilter}
          setSorting={setSorting}
          refetch={refetch}
        />
      )}
      {data && (
        <KdsContent>
          {data?.list.length === 0 ? (
            <div className="noOrder">
              <div className="title">
                <img src="/images/intro-title.png" alt="1인피자의 시작" />
              </div>
              <div className="logo">
                <img src="/images/intro-logo.png" alt="GOPIZZA" />
              </div>
            </div>
          ) : (
            <SwiperContainer
              className="mySwiper"
              onSwiper={setSwiperRef}
              cssMode={true}
              mousewheel={true}
              onSlideChange={() => setSlideIdx(Number(swiperRef?.realIndex))}
              modules={[Mousewheel]}>
              {new Array(Math.ceil(data.list.length / grid))
                .fill(null)
                .map((_, i) => (
                  <SwiperSlide key={i}>
                    <SwiperSlideItem
                      grid={grid}
                      receiptsData={data.list.slice(grid * i, grid * (i + 1))}
                    />
                  </SwiperSlide>
                ))}
            </SwiperContainer>
          )}
        </KdsContent>
      )}
      <GlobalModal />
    </KdsWrap>
  );
}

export default observer(Home);
